@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.movies {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-auto-rows: 100vh;
  grid-gap: 2px;
  gap: 2px;
}

.movie {
  background-color: var(--main--bg--color);
  display: flex;
  flex-direction: column;
  transition: all 0.1s linear;
}

.movie:hover {
  cursor: pointer;
  background-color: var(--sub--bg--color);
  color: var(--main--bg--color);
  font-style: italic;
}

.movie img {
  height: 66vh;
  object-fit: cover;
}

.movie__data {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 30px;
  grid-gap: 20px;
  gap: 20px;
}

.movie__summary {
  text-align: justify;
  line-height: 1.2;
}

.movie__genres {
  display: flex;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.genres__genre {
  border: 1px var(--sub--bg--color) solid;
  padding: 5px 10px;
  border-radius: 25px;
  font-size: 14px;
}

.header {
  position: fixed;
  height: 50px;
  width: 100%;
  background-color: inherit;
  top: 0;
  color: var(--sub--bg--color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  padding: 50px 50px;
}

.header__title {
  background-color: inherit;
  border: 1px var(--sub--bg--color) solid;
  border-radius: 50%;
  transition: all 0.1s linear;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.header__title:hover {
  cursor: pointer;
  background-color: var(--sub--bg--color);
  border-color: var(--main--bg--color);
  color: var(--main--bg--color);
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --main--bg--color: #2f2f2f;
  --sub--bg--color: #f2f2f2;
}

body {
  width: 100%;
  background-color: #f2f2f2;
  background-color: var(--sub--bg--color);
  color: #f2f2f2;
  color: var(--sub--bg--color);
  font-family: "Playfair Display", serif;
}

.container > .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #2f2f2f;
  color: var(--main--bg--color);
}

h3 {
  font-size: 28px;
  font-weight: 600;
  letter-spacing: 2px;
}

